.nav {
    grid-area: n;
    background-color: #33CBFBFF;
    padding: 0 0 0 20px;


}

.nav li {
    list-style-type: none;
    cursor: pointer;
}
.nav a {
    color: #fff;
    text-decoration: none;
    cursor: pointer;
}
.nav a.active {
    color: gold;
    cursor: pointer;

}

.nav ul {
    margin-left: 0;
    padding-left: 0;
}
